/* music-checkbox  */
input {
    display:none
}

i {
    position: absolute;
    top: 6px;
    left: 5px;
    color: #30211e;
    text-shadow: 0px 1px 0px #ccc2a6;
}

.toggle {
    position: relative;
    display: block;
    width: 80px;
    height: 36px;
    border-radius: 50%;
    cursor: pointer;
    /* transform: translate3d(0, 0, 0); */
}

.toggle:before {
    content: "";
    position: relative;
    top: 1px;
    left: 1px;
    width: 80px;
    height: 36px;
    display: block;
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #8a550b), to(#e9ad58));
    background:-webkit-linear-gradient(top, #8a550b 5%, #e9ad58 100%);
    background:-o-linear-gradient(top, #8a550b 5%, #e9ad58 100%);
    background:linear-gradient(to bottom, #8a550b 5%, #e9ad58 100%);
    border: 2px solid #1c1b18d6;
    border-radius: 30px;
    -webkit-transition: background 0.2s ease;
    -o-transition: background 0.2s ease;
    transition: background 0.2s ease;
}

.toggle span {
    content: "";
    position: absolute;
    top: 5.6px;
    left: 5.5px;
    width: 28px;
    height: 26px;
    display: block;
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #e9ad58), to(#8a550b));
    background:-webkit-linear-gradient(top, #e9ad58 5%, #8a550b 100%);
    background:-o-linear-gradient(top, #e9ad58 5%, #8a550b 100%);
    background:linear-gradient(to bottom, #e9ad58 5%, #8a550b 100%);
    -webkit-box-shadow: 0 3px 8px #5d3908;
            box-shadow: 0 3px 8px #5d3908;
    border-radius: 50%;
    -webkit-transition: all 0.2s ease;
    -o-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.toggle span:before {
    content: "";
    position: absolute;
    top: 5px;
    left: 5.5px;
    width: 28px;
    height: 28px;
    display: block;
    /* margin-top: 5px; */
    /* width: 60px;
    height: 60px; */
    /* border-radius: 50%; */
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
    opacity: 1;
    pointer-events: none;
}

#cbx:checked + .toggle:before {
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #1b1311), to(#cf9849));
    background:-webkit-linear-gradient(top, #1b1311 5%, #cf9849 100%);
    background:-o-linear-gradient(top, #1b1311 5%, #cf9849 100%);
    background:linear-gradient(to bottom, #1b1311 5%, #cf9849 100%);
    border: 2px solid #1c1b18d6;
 }

#cbx:checked + .toggle span {
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #e9ad58), to(#8a550b));
    background:-webkit-linear-gradient(top, #e9ad58 5%, #8a550b 100%);
    background:-o-linear-gradient(top, #e9ad58 5%, #8a550b 100%);
    background:linear-gradient(to bottom, #e9ad58 5%, #8a550b 100%);
    -webkit-transform: translateX(42px);
        -ms-transform: translateX(42px);
            transform: translateX(42px);
    -webkit-transition: all 0.2s cubic-bezier(0.8, 0.4, 0.3, 1.25), background 0.15s ease;
    -o-transition: all 0.2s cubic-bezier(0.8, 0.4, 0.3, 1.25), background 0.15s ease;
    transition: all 0.2s cubic-bezier(0.8, 0.4, 0.3, 1.25), background 0.15s ease;
    -webkit-box-shadow: 0 3px 8px #5d3908;
            box-shadow: 0 3px 8px #5d3908;
}

#cbx:checked + .toggle span:before {
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    opacity: 0;
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
}


