.modalPop {
    -webkit-animation: rowing 4000ms infinite;
            animation: rowing 4000ms infinite;
}

.modal-content {
    color: #30211e;
    background: transparent !important;
    border: none;
}

@-webkit-keyframes rowing {
    0% { -webkit-box-shadow: 0 0 10px rgba(248, 162, 3, 0.884); box-shadow: 0 0 10px rgba(248, 162, 3, 0.884); }
    40% { -webkit-box-shadow: 0 0 40px rgba(248, 162, 3, 0.884); box-shadow: 0 0 40px rgba(248, 162, 3, 0.884); }
    60% { -webkit-box-shadow: 0 0 480px rgba(248, 162, 3, 0.884); box-shadow: 0 0 480px rgba(248, 162, 3, 0.884); }
    100% { -webkit-box-shadow: 0 0 10px rgba(248, 162, 3, 0.884); box-shadow: 0 0 10px rgba(248, 162, 3, 0.884); }
}

@keyframes rowing {
    0% { -webkit-box-shadow: 0 0 10px rgba(248, 162, 3, 0.884); box-shadow: 0 0 10px rgba(248, 162, 3, 0.884); }
    40% { -webkit-box-shadow: 0 0 40px rgba(248, 162, 3, 0.884); box-shadow: 0 0 40px rgba(248, 162, 3, 0.884); }
    60% { -webkit-box-shadow: 0 0 480px rgba(248, 162, 3, 0.884); box-shadow: 0 0 480px rgba(248, 162, 3, 0.884); }
    100% { -webkit-box-shadow: 0 0 10px rgba(248, 162, 3, 0.884); box-shadow: 0 0 10px rgba(248, 162, 3, 0.884); }
}



