.landCardWorld {
    width: 4.5em;
}

.landCardWorld {
    -webkit-box-shadow: 17px 0px 36px -4px rgba(0,0,0,0.75);
            box-shadow: 17px 0px 36px -4px rgba(0,0,0,0.75);
    -webkit-transition: all 0.1s ;
    -o-transition: all 0.1s ;
    transition: all 0.1s ;
}

.landCardWorld:hover {
    -webkit-transform: scale(1.05)translateY(-0.1em);
        -ms-transform: scale(1.05)translateY(-0.1em);
            transform: scale(1.05)translateY(-0.1em);
    -webkit-box-shadow: 0px 0px 100px -8px rgba(0,0,0,0.75);
            box-shadow: 0px 0px 100px -8px rgba(0,0,0,0.75);
}
.landCardWorld:active {
    -webkit-transform: scale(1.15)translateY(-0.3em);
        -ms-transform: scale(1.15)translateY(-0.3em);
            transform: scale(1.15)translateY(-0.3em);
    -webkit-box-shadow: 0px 0px 100px -8px rgba(248, 162, 3, 0.884);
            box-shadow: 0px 0px 100px -8px rgba(248, 162, 3, 0.884);
}

@-webkit-keyframes glowing {
    0% { -webkit-box-shadow: 0 0 20px rgba(248, 162, 3, 0.884); box-shadow: 0 0 20px rgba(248, 162, 3, 0.884); }
    40% { -webkit-box-shadow: 0 0 60px rgba(248, 162, 3, 0.884); box-shadow: 0 0 60px rgba(248, 162, 3, 0.884); }
    60% { -webkit-box-shadow: 0 0 60px rgba(248, 162, 3, 0.884); box-shadow: 0 0 60px rgba(248, 162, 3, 0.884); }
    100% { -webkit-box-shadow: 0 0 20px rgba(248, 162, 3, 0.884); box-shadow: 0 0 20px rgba(248, 162, 3, 0.884); }
}

@keyframes glowing {
    0% { -webkit-box-shadow: 0 0 20px rgba(248, 162, 3, 0.884); box-shadow: 0 0 20px rgba(248, 162, 3, 0.884); }
    40% { -webkit-box-shadow: 0 0 60px rgba(248, 162, 3, 0.884); box-shadow: 0 0 60px rgba(248, 162, 3, 0.884); }
    60% { -webkit-box-shadow: 0 0 60px rgba(248, 162, 3, 0.884); box-shadow: 0 0 60px rgba(248, 162, 3, 0.884); }
    100% { -webkit-box-shadow: 0 0 20px rgba(248, 162, 3, 0.884); box-shadow: 0 0 20px rgba(248, 162, 3, 0.884); }
}
  
.glowLand {
    -webkit-animation: glowing 4000ms infinite;
            animation: glowing 4000ms infinite;
}


