.flightCardHand {
    margin-right: 0.5rem;
    width: 5rem;
    -webkit-box-shadow: 0px 0px 42px 1px rgba(0,0,0,0.75);
            box-shadow: 0px 0px 42px 1px rgba(0,0,0,0.75);
    -webkit-transition: all 0.1s ease;
    -o-transition: all 0.1s ease;
    transition: all 0.1s ease;
}

.flightCardHand:hover {
    -webkit-transform: rotate(2.8deg)scale(1.25)translateY(-1em);
        -ms-transform: rotate(2.8deg)scale(1.25)translateY(-1em);
            transform: rotate(2.8deg)scale(1.25)translateY(-1em);
    /* box-shadow: 0px 0px 70px 4px rgba(248, 162, 3, 0.75); */
    -webkit-animation: glowflight 2000ms infinite;
            animation: glowflight 2000ms infinite;
}

.flightCardHand:active {
    -webkit-transform: rotate(2.8deg)scale(1.4)translateY(-2em);
        -ms-transform: rotate(2.8deg)scale(1.4)translateY(-2em);
            transform: rotate(2.8deg)scale(1.4)translateY(-2em);
}

@-webkit-keyframes glowflight {
    0% { -webkit-box-shadow: 0 0 25px rgba(248, 162, 3, 0.75); box-shadow: 0 0 25px rgba(248, 162, 3, 0.75); }
    40% { -webkit-box-shadow: 0 0 80px rgba(248, 162, 3, 0.75); box-shadow: 0 0 80px rgba(248, 162, 3, 0.75); }
    60% { -webkit-box-shadow: 0 0 80px rgba(248, 162, 3, 0.75); box-shadow: 0 0 80px rgba(248, 162, 3, 0.75); }
    100% { -webkit-box-shadow: 0 0 25px rgba(248, 162, 3, 0.75); box-shadow: 0 0 25px rgba(248, 162, 3, 0.75); }
}

@keyframes glowflight {
    0% { -webkit-box-shadow: 0 0 25px rgba(248, 162, 3, 0.75); box-shadow: 0 0 25px rgba(248, 162, 3, 0.75); }
    40% { -webkit-box-shadow: 0 0 80px rgba(248, 162, 3, 0.75); box-shadow: 0 0 80px rgba(248, 162, 3, 0.75); }
    60% { -webkit-box-shadow: 0 0 80px rgba(248, 162, 3, 0.75); box-shadow: 0 0 80px rgba(248, 162, 3, 0.75); }
    100% { -webkit-box-shadow: 0 0 25px rgba(248, 162, 3, 0.75); box-shadow: 0 0 25px rgba(248, 162, 3, 0.75); }
}



